import React, { Component } from "react";
import Layout from "../../../components/Layout/Layout";
import Grid from "@material-ui/core/Grid";
import classes from "./AddMember.module.css";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import Button from "@material-ui/core/Button";
import Modal from "../../../components/Modal/Modal";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Task from "../../../components/Task/TaskBar";
import axios from "axios";

class AddMember extends Component {
  constructor(props) {
    super(props);
    this.state = {
      addNewMemeber: false,
      newMemeber: {
        role: "DEVELOPER",
        FirstName: "",
        LastName: "",
        Email: "",
      },
      curList: [{ role: "", FirstName: "", LastName: "", Email: "" }],
    };
    this.closeAddMemberModal = this.closeAddMemberModal.bind(this);
    this.handleRoleChange = this.handleRoleChange.bind(this);
    this.updateNewMember = this.updateNewMember.bind(this);
    this._handleEmailChange = this._handleEmailChange.bind(this);
  }

  showAddMemberModal() {
    this.setState({ addNewMemeber: true });
  }

  closeAddMemberModal() {
    this.setState({ addNewMemeber: false });
  }

  _handleEmailChange(event) {
    this.setState({
      newMemeber: {
        role: this.state.newMemeber.role,
        FirstName: this.state.newMemeber.FirstName,
        LastName: this.state.newMemeber.LastName,
        Email: event.target.value,
      },
    });
  }

  updateNewMember() {
    console.log("###After Modal: " + JSON.stringify(this.state.newMemeber));
    //this.state.curList.push(this.state.newMemeber);
    let postData = {
      role: "",
      email: "",
      workspaceId: "2e2f20e0-8300-4695-9b79-56d8a6fa5622",
    };

    const config = {
      headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
    };

    postData.email = this.state.newMemeber.Email;
    postData.role = this.state.newMemeber.role;
    const endpoint = "localhost:8080/workspace/adduser";
    axios.post(endpoint, postData, config).then((res) => {
      console.log(res);
    });

    console.log("POST DATA: " + JSON.stringify(postData));
    this.setState({
      addNewMemeber: false,
      curList: [...this.state.curList, this.state.newMemeber],
    });
  }

  handleRoleChange(role) {
    this.setState({
      newMemeber: {
        role: role,
        FirstName: this.state.newMemeber.FirstName,
        LastName: this.state.newMemeber.LastName,
        Email: this.state.newMemeber.Email,
      },
    });
  }

  render() {
    return (
      <Layout rolename="admin">
        <Grid
          style={{
            marginTop: 20,
            marginLeft: 100,
            marginRight: 100,
          }}
          item
          xs={12}
        >
          <div className={classes.TitleAndSearch}>
            <div className={classes.Title}>Team</div>
            <Button
              variant="contained"
              startIcon={<AccountCircleIcon />}
              type="submit"
              value="Submit"
              onClick={() => {
                this.showAddMemberModal();
              }}
              style={{
                marginLeft: 10,
                marginTop: 30,
              }}
            >
              Invite New Member
            </Button>
          </div>

          <Grid
            container
            direction="row"
            justify="space-evenly"
            spacing={0}
            style={{
              height: 700,
              background: "White",
              borderRadius: 10,
              color: "#5D5D5B",
            }}
            item
            xs={12}
          >
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.TaskTitle}>Admin</div>
              {this.state.curList.map((item) => {
                if (item.role === "ADMIN")
                  return (
                    <Task
                      key={item.Email}
                      //className={classes.Task}
                      taskname={item.Email}
                      //clicked={() => this.directToTask(item)}
                    />
                  );
              })}
            </Grid>

            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.TaskTitle}>Designer</div>
              {this.state.curList.map((item) => {
                if (item.role === "DESIGNER")
                  return (
                    <Task
                      key={item.Email}
                      //className={classes.Task}
                      taskname={item.Email}
                      //clicked={() => this.directToTask(item)}
                    />
                  );
              })}
            </Grid>

            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                marginRight: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.TaskTitle}>Developer</div>
              {this.state.curList.map((item) => {
                if (item.role === "DEVELOPER")
                  return (
                    <Task
                      key={item.Email}
                      //className={classes.Task}
                      taskname={item.Email}
                      //clicked={() => this.directToTask(item)}
                    />
                  );
              })}
            </Grid>
          </Grid>
        </Grid>
        <Modal
          show={this.state.addNewMemeber}
          modalClosed={this.closeAddMemberModal}
        >
          <form className={classes.form} noValidate>
            <div className={classes.ModalTitle}>Member Info</div>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={6}>
                <TextField
                  autoComplete="fname"
                  name="firstName"
                  variant="outlined"
                  fullWidth
                  id="firstName"
                  label={"FirstName"}
                  autoFocus
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  variant="outlined"
                  fullWidth
                  id="lastName"
                  label={"LastName"}
                  name="lastName"
                  autoComplete="lname"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  id="email"
                  label={"Email"}
                  name="email"
                  value={this.state.newMemeber.Email}
                  onChange={this._handleEmailChange}
                  autoComplete="email"
                />
              </Grid>
            </Grid>
            <Grid item xs={12}>
              <TextField
                select
                fullWidth
                label={"Role"}
                autoComplete={this.state.newMemeber.role}
                required
                helperText="Please select role"
                style={{ marginTop: 10 }}
              >
                <MenuItem onClick={() => this.handleRoleChange("ADMIN")}>
                  Admin
                </MenuItem>
                <MenuItem onClick={() => this.handleRoleChange("DESIGNER")}>
                  Designer
                </MenuItem>
                <MenuItem onClick={() => this.handleRoleChange("DEVELOPER")}>
                  Developer
                </MenuItem>
              </TextField>
            </Grid>
            <Button
              //type="submit"
              fullWidth
              variant="contained"
              color="primary"
              //className={classes.submit}
              style={{
                marginTop: 10,
                marginBottom: 30,
              }}
              onClick={() => this.updateNewMember()}
            >
              Invite
            </Button>
          </form>
        </Modal>
      </Layout>
    );
  }
}

export default AddMember;
