/* eslint-disable react/prop-types */
import React, { Component } from "react";

import classes from "./Workspaces.module.css";
import Workspace from "../../components/Workspace/WorkspaceBar";
import Layout from "../../components/Layout/Layout";
import Footer from "../../components/Footer/Footer";
import {
  SERVER_USER_WORKSPACES,
  DEVELOPER_DETAILS_URL,
  DESIGNER_URL,
} from "../../common/Url";
import { UserRoles } from "../../common/UserRoles";

import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import IconButton from "@material-ui/core/IconButton";
import InputAdornment from "@material-ui/core/InputAdornment";
import SearchIcon from "@material-ui/icons/Search";
import Button from "@material-ui/core/Button";
import AddCircleIcon from "@material-ui/icons/AddCircle";

import axios from "axios";
import { connect } from "react-redux";
import * as actionTypes from "../../store/actions";
import { FormattedMessage, injectIntl } from "react-intl";

class Workspaces extends Component {
  constructor(props) {
    super(props);
    this.state = {
      newWorkspace: [],
      filter: "",
    };
    this.handleChange = this.handleChange.bind(this);
  }

  componentDidMount() {
    const url = SERVER_USER_WORKSPACES;
    axios
      .get(url)
      .then((response) => response.data)
      .then((data) => {
        this.props.updateAllWorkspaces(data);
        console.log(data);
        console.log("##############################check state ######");
        console.log(this.props.newWorkspaces);
      });
  }

  handleChange(event) {
    this.setState({ [event.target.name]: event.target.value });
  }

  directToWorkspace(data) {
    this.props.updateCurrentWorkspace(data);
    if (data.role === UserRoles.DEVELOPER)
      this.props.history.push(DEVELOPER_DETAILS_URL);
    if (data.role === UserRoles.DESIGNER) this.props.history.push(DESIGNER_URL);
  }

  render() {
    return (
      <Layout rolename="workspaces">
        <Grid
          style={{
            marginTop: 80,
            marginLeft: 100,
            marginRight: 100,
          }}
          item
          xs={12}
        >
          {/* title area */}
          <div className={classes.TitleAndSearch}>
            <div className={classes.Title}>
              <FormattedMessage id="workspaces.allWorkspaces"></FormattedMessage>
            </div>
          </div>

          {/* button and search area */}
          <Grid
            container
            direction="row"
            justify="space-between"
            spacing={0}
            item
            xs={12}
          >
            <Grid
              style={{
                marginTop: 20,
                marginBottom: 20,
              }}
              item
              xs={4}
            >
              <Button
                variant="contained"
                startIcon={<AddCircleIcon />}
                // onClick={() => this.saveDiagram()}
              >
                <FormattedMessage id="workspaces.create"></FormattedMessage>
              </Button>
            </Grid>
            <Grid
              style={{
                marginTop: 20,
                marginBottom: 20,
              }}
              item
              xs={6}
            >
              <TextField
                style={{
                  display: "flex",
                  borderColor: "white",
                  border: 0,
                  borderRadius: 3,
                  backgroundColor: "white",
                  paddingLeft: 10,
                  paddingRight: 10,
                }}
                placeholder={this.props.intl.formatMessage({
                  id: "workspaces.search",
                })}
                variant="standard"
                name="filter"
                value={this.state.filter}
                onChange={this.handleChange}
                InputProps={{
                  startAdornment: (
                    <InputAdornment>
                      <IconButton>
                        <SearchIcon />
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
          </Grid>

          {/* workspaces area */}
          <Grid
            container
            direction="row"
            justify="space-evenly"
            spacing={0}
            style={{
              height: 700,
              background: "#5D5D5B",
              borderRadius: 10,
              color: "#5D5D5B",
            }}
            item
            xs={12}
          >
            {/* admin workspace area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.WorkspaceTitle}>
                <FormattedMessage id="workspaces.admin.workspaces"></FormattedMessage>
              </div>
              {this.props.newWorkspaces.map((item) => {
                if (
                  item.role === UserRoles.ADMIN &&
                  item.name
                    .toLowerCase()
                    .includes(this.state.filter.toLowerCase())
                )
                  return (
                    <Workspace
                      key={item.id}
                      workspacename={item.name}
                      clicked={() => this.directToWorkspace(item)}
                    />
                  );
              })}
            </Grid>
            {/* designer workspaces area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.WorkspaceTitle}>
                <FormattedMessage id="workspaces.designer.workspaces"></FormattedMessage>
              </div>
              {this.props.newWorkspaces.map((item) => {
                if (
                  item.role === UserRoles.DESIGNER &&
                  item.name
                    .toLowerCase()
                    .includes(this.state.filter.toLowerCase())
                )
                  return (
                    <Workspace
                      key={item.id}
                      workspacename={item.name}
                      clicked={() => this.directToWorkspace(item)}
                    />
                  );
              })}
            </Grid>
            {/* developer workspaces area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                marginRight: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.WorkspaceTitle}>
                <FormattedMessage id="workspaces.developer.workspaces"></FormattedMessage>
              </div>
              {this.props.newWorkspaces.map((item) => {
                if (
                  item.role === UserRoles.DEVELOPER &&
                  item.name
                    .toLowerCase()
                    .includes(this.state.filter.toLowerCase())
                )
                  return (
                    <Workspace
                      key={item.id}
                      workspacename={item.name}
                      clicked={() => this.directToWorkspace(item)}
                    />
                  );
              })}
            </Grid>
          </Grid>
        </Grid>
        {/* footer */}
        <Footer />
      </Layout>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    newWorkspaces: state.allNewWorkspaces,
    filter: state.filter,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateCurrentWorkspace: (currentWorkspace) =>
      dispatch({
        type: actionTypes.UPDATE_CURRENT_WORKSPACE,
        data: currentWorkspace,
      }),
    updateAllWorkspaces: (allWorkspace) =>
      dispatch({
        type: actionTypes.UPDATE_ALL_NEWWORKSPACES,
        data: allWorkspace,
      }),
  };
};

export default injectIntl(
  connect(mapStateToProps, mapDispatchToProps)(Workspaces)
);
